import { Injectable } from '@angular/core';
import { ActivatedRoute, NavigationExtras, Router } from '@angular/router';

/** @internal */
@Injectable()
export class StopChecksNavigationService {
  private defaultRouteParams = { relativeTo: this.route };
  constructor(
    private readonly router: Router,
    private readonly route: ActivatedRoute
  ) {}

  form(extras: NavigationExtras = {}): void {
    this.router.navigate(['..', 'form'], {
      ...this.defaultRouteParams,
      ...extras,
    });
  }

  review(extras: NavigationExtras = {}): void {
    this.router.navigate(['review'], { ...this.defaultRouteParams, ...extras });
  }
}
